  import axios from 'axios'
  import HomeSearch from '../../components/searchBar.vue'
  import HomeSwiper from './components/swiper.vue'
  import NavSwiper from './components/navSwiper.vue'
  import AdvertSwiper from './components/advertSwiper.vue'
  import HomeList from './components/list.vue'
//  import loading from '../../components/loading.vue'
  export default {
    name: 'homeIndex',
    data() {
      return {
        isLoading: true,
        banner: [], //轮播图
        choice: {}, //平台精选
        newList: {}, //新品推荐
        cart: 0, //
        information: [],//来点快讯
        menu: [],//menu
        openId: '',
      }
    },
    components: {
      HomeSearch,
      HomeSwiper,
      NavSwiper,
      AdvertSwiper,
      HomeList,
//      loading
    },
    created (){
      this.openId = this.$store.state.openId
      this.getIndexData()
    },
    methods: {
      showToastTxtOnly(text) {
        this.toast = this.$createToast({
          txt: text,
          type: 'txt'
        })
        this.toast.show()
      },
      getIndexData(){
        this.http.get(this.ports.home.index, res =>{
          this.isLoading = false
          this.$store.commit('changeLoading',false)
          if(res.success){
            let data = res.data
            this.banner = data.banner
            this.choice = data.choice
            this.newList = data.new
            this.cart = data.cart
            this.menu = data.menu
            this.information = data.information
            this.$store.commit('changeCartNum',this.cart)
          }else{
            this.showToastTxtOnly(res.msg)
          }
        })
      }
    },
    watch:{
      openId(){
        this.getIndexData()
      }
    },
    mounted (){
    }
  }



